import './_vars.js';
import './_functions.js';
import './_components.js';

// Get height of header

const getHeaderHeight = () => {
  const headerHeight = document?.querySelector('.header').offsetHeight;
  document.querySelector(':root').style.setProperty('--header-height', `${headerHeight}px`);
}

import TransferElements from 'transfer-elements';

// Transformation of elements of header to in mobile menu

new TransferElements(
  {
    sourceElement: document.querySelector('.header__profile'),
    breakpoints: {
      1023.98: {
        targetElement: document.querySelector('.header__mobile-profile'),
      }
    }
  }
);

new TransferElements(
  {
    sourceElement: document.querySelector('.header__lang'),
    breakpoints: {
      1023.98: {
        targetElement: document.querySelector('.header__mobile-lang'),
      }
    }
  }
);

new TransferElements(
  {
    sourceElement: document.querySelector('.menu-logged'),
    breakpoints: {
      480.98: {
        targetElement: document.querySelector('.header__nav'),
        targetPosition: 0,
      }
    }
  }
);

new TransferElements(
  {
    sourceElement: document.querySelector('.summary'),
    breakpoints: {
      767.98: {
        targetElement: document.querySelector('.submit'),
      }
    }
  }
);

if(document.querySelectorAll('.header').length > 0) {

  // Scrolling header

  const header = document.querySelector('.header');

  function scrolling() {
    if (document.documentElement.scrollTop > 10 || document.body.scrollTop > 10) {
      header.classList.add('scrolling');
    } else {
      header.classList.remove('scrolling');
    }
  }

  window.addEventListener('scroll', () => {
    scrolling();
  })

  // click to authorize

  const btnAuth = document.querySelector('.header__profile');

  btnAuth.addEventListener('click', () => {
    header.classList.add('logged');
  })

}

// Select languages

const headerLang = document.querySelector('.js-choice');
const choices = new Choices(headerLang, {
  searchEnabled: false,
  itemSelectText: '',
  shouldSort: false,
});

// Get height of header

getHeaderHeight();

// Logged in menu

document.addEventListener("DOMContentLoaded", () => {

  // Get height of header

  getHeaderHeight();

  // Dropdown menu

  const menuButtons = document.querySelectorAll('[data-js-dropdown-button]');
  const dropDownMenus = document.querySelectorAll('[data-js-dropdown-menu]')

  menuButtons.forEach(btn  => {
    btn.addEventListener('click', (e) => {
      let currentButton = e.currentTarget;
      let currentDropdown = currentButton.closest('.menu-logged__item').querySelector('[data-js-dropdown-menu]');

      menuButtons.forEach(btn => {
        if (btn !== currentButton) {
          btn.classList.remove('menu-logged__btn-active');
        }
      })

      dropDownMenus.forEach(drop => {
        if (drop !== currentDropdown) {
          drop.classList.remove('dropdown-active');
        }
      })

      btn.classList.toggle('menu-logged__btn-active');
      currentDropdown.classList.toggle('dropdown-active')
    })
  })

  document.addEventListener('click', (e) => {
    if (!e.target.closest('.menu-logged')) {

      menuButtons.forEach(btn => {
        btn.classList.remove('menu-logged__btn-active');
      })

      dropDownMenus.forEach(drop => {
        drop.classList.remove('dropdown-active');
      })
    }
  })


  // Multi steps

  if (document.querySelectorAll('.product').length > 0) {

    const progress = document.querySelector('.progress');
    const stepIndicators = document.querySelectorAll('.progress-container li');
    const steps = document.querySelectorAll('.step');
    const btnBooking = document.querySelector('.btn-booking');
    const btnContinue = document.querySelector('.btn-continue');
    const buttonsPay = document.querySelector('.buttons-pay');
    const progressContainer = document.querySelector('.progress-container');
    const stepsContainer = document.querySelector(".steps-container");
    const stepsTop = document.querySelector(".step-top");

    document.documentElement.style.setProperty("--steps", stepIndicators.length);

    let currentStep = 0;

    let screenWidth = window.matchMedia('(max-width:767.98px)');

    const updateProgress = () => {
      let width = currentStep / (steps.length - 1);
      progress.style.transform = `scaleX(${width})`;

      if (screenWidth.matches) {
        stepsContainer.style.height = steps[currentStep].offsetHeight + "px";
      }

        stepIndicators.forEach((indicator, index) => {
        indicator.classList.toggle("current", currentStep === index);
        indicator.classList.toggle("done", currentStep > index);
      });

      steps.forEach((step, index) => {
        step.style.transform = `translateX(-${currentStep * 100}%)`;
        step.classList.toggle("current", currentStep === index);
      });

      updateButtons();
    };


    const updateButtons = () => {
      //btnBooking.hidden = currentStep === 0;
      btnContinue.hidden = currentStep >= steps.length - 1;
      buttonsPay.hidden = !btnContinue.hidden;
    };

    //* event listeners

    btnBooking.addEventListener("click", (e) => {
      e.preventDefault();

      steps.forEach(step => {
        step.classList.remove('hide');
      })
      stepsTop.style.display = 'none'
      progressContainer.classList.remove('hide');
      btnContinue.classList.remove('hide');

      btnBooking.classList.add('hide');
    });

    btnContinue.addEventListener("click", (e) => {
      e.preventDefault(); // prevent form submission

      if (currentStep < steps.length - 1) {
        currentStep++;
        updateProgress();
      }
    });

    updateProgress();
  }
})

// Get year for copyright

if (document.querySelectorAll('.footer').length > 0) {

  const date = new Date();
  const elementForCurrentYear = document.querySelector('.footer__get-year');
  const currentYear = date.getFullYear();

  elementForCurrentYear.innerHTML = currentYear;

}
